"use client";

import React from "react";
import Link from "next/link";
import { Button } from "antd";
import Image from "next/image";
import logo from "../assets/logo.webp";
const Navbar: React.FC = () => {
  return (
    <nav className="bg-[#232536] border-gray-200 py-2.5 ">
      <div className="flex flex-wrap items-center justify-between px-4 mx-auto  max-w-[1440px] py-3">
        {/* Logo */}
        <Link href="/" className="flex items-center">
        <Image src={logo} alt="Logo" width={50} height={50} />
          <span className="text-white text-[20px] font-bold pl-2 ">
            Company Logo
          </span>
        </Link>

        {/* Right Section */}
        <div className="flex items-center "></div>

        {/* Menu */}
        <div
          className="items-center justify-between w-full lg:flex lg:w-auto "
          id="mobile-menu-2"
        >
          <ul className="flex flex-col items-center mt-4 font-medium lg:flex-row lg:space-x-8 !mb-0">
            <li>
              <Link
                href="/"
                className=" py-2 pl-3 pr-4 text-white 
               "
              >
                Home
              </Link>
            </li>
            <li>
              <Link href="/company" className=" py-2 px-4 text-white">
                Blogs
              </Link>
            </li>
            <li>
              <Link href="/contact" className=" py-2 px-4 text-white">
                About
              </Link>
            </li>
            <li>
              <Link href="/contact" className=" py-2 px-4 text-white">
                Contact Us
              </Link>
            </li>
            <li>
              <Button
                type="dashed"
                className="bg-purple-700 !py-5 !text-[18px] font-roboto !hover:bg-purple-800"
              >
                Download
              </Button>
            </li>
          </ul>
        </div>
      </div>
    </nav>
  );
};

export default Navbar;
